<template>
  <div class="container">

    <md-input-container>
      <label>Username</label>
      <md-input placeholder="Username" v-model="username"></md-input>
    </md-input-container>

    <md-table md-sort-type="desc" @sort="sortRepos">
      <md-table-header>
        <md-table-row>
          <md-table-head md-sort-by="name">
            Name
          </md-table-head>
          <md-table-head>
            Description
          </md-table-head>
          <md-table-head md-sort-by="stargazers_count">
            Number of Stars
          </md-table-head>
          <md-table-head md-sort-by="forks_count">
            Number of Forks
          </md-table-head>
          <md-table-head md-sort-by="open_issues_count">
            Number of Open Issues
          </md-table-head>
          <md-table-head md-sort-by="language">
            Language
          </md-table-head>
          <md-table-head md-sort-by="size">
            Size
          </md-table-head>
        </md-table-row>
      </md-table-header>

      <md-table-body>
        <md-table-row v-for="repo in repos" :key="repo">
          <md-table-cell>
            <a :href="repo.html_url" target="_blank">{{ repo.name }}</a>
          </md-table-cell>
          <md-table-cell>
            {{ repo.description }}
          </md-table-cell>
          <md-table-cell>
            {{ repo.stargazers_count }}
          </md-table-cell>
          <md-table-cell>
            {{ repo.forks_count }}
          </md-table-cell>
          <md-table-cell>
            {{ repo.open_issues_count }}
          </md-table-cell>
          <md-table-cell>
            {{ repo.language }}
          </md-table-cell>
          <md-table-cell>
            {{ repo.size }}Kb
          </md-table-cell>
        </md-table-row>
        <md-table-row v-if="repos.length === 0">
          <md-table-cell colspan="7">
            No repos found...
          </md-table-cell>
        </md-table-row>
      </md-table-body>
    </md-table>
  </div>
</template>

<script>
  import _ from 'lodash';

  export default {
    name: 'GithubCrawler',
    data() {
      return {
        username: null,
        repos: [],
      };
    },
    watch: {
      username(username) {
        this.setUsername(username);
      }
    },
    methods: {
      sortRepos(sorting) {
        if (sorting.type === 'asc') {
          this.sortAsc(sorting.name);
        }

        if (sorting.type === 'desc') {
          this.sortDesc(sorting.name);
        }
      },
      sortAsc(column) {
        this.repos.sort((a, b) => {
          if (a[column] > b[column]) {
            return 1;
          }

          if (a[column] < b[column]) {
            return -1;
          }

          return 0
        });
      },
      sortDesc(column) {
        this.repos.sort((a, b) => {
          if (a[column] > b[column]) {
            return -1;
          }

          if (a[column] < b[column]) {
            return 1;
          }

          return 0
        });
      },
      fetchRepos(username) {
        this.$http.get(`users/${username}/repos`)
          .then(({ data }) => {
            this.repos = data;
          });
      },
      setUsername: _.debounce(function (username) {
        if (username) {
          this.fetchRepos(username);
        }

        if (!username) {
          this.repos = [];
        }
      }, 500),
    },
  };
</script>



